import React from 'react';
import { AppContext } from '../../App';

const Slider = () => {
  const { setCartState } = React.useContext(AppContext);
  return (
    <div className="sliderbox">
      <img
        onClick={() => setCartState(true)}
        className="slider"
        src="img/slider.png"
        alt="slider"
      />
    </div>
  );
};

export default Slider;
